import { Component, OnInit } from '@angular/core'; import { Router } from '@angular/router';  @Component({   moduleId: module.id,   selector: 'profissao-dados',   templateUrl: 'profissao_dados.component.html' }) export class ProfissaoDadosComponent implements OnInit {       displayMode: ProfissaoDadosDisplayModeEnum;     displayModeEnum = ProfissaoDadosDisplayModeEnum;       constructor(private router: Router) { }      ngOnInit() {       const path = this.router.url.split('/')[3];       switch (path) {         case 'details':           this.displayMode = ProfissaoDadosDisplayModeEnum.Details;           break;         case 'info':           this.displayMode = ProfissaoDadosDisplayModeEnum.Orders;           break;         case 'edit':           this.displayMode = ProfissaoDadosDisplayModeEnum.Edit;           break;       }     }  }  enum ProfissaoDadosDisplayModeEnum {   Details=0,   Orders=1,   Edit=2 }


